@import "../../mixing.scss";
.footer {
  background-image: url(../../assets/images/footer-bg-min.webp);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  .widget {
    padding-left: 5px;
    padding-right: 5px;
    @media (min-width: 576px) {
      padding-left: 30px;
      padding-right: 30px;
    }
    @media (min-width: 992px) {
      padding-left: 0;
      padding-right: 0;
    }
  }
  .contactUs {
    padding-bottom: 0;
    .contacts {
      margin-bottom: 30px;
      a {
        transition: all 0.2s linear;
        &:not(.btn-pink):hover {
          text-decoration: underline !important;
          color: var(--clr-green);
        }
      }
      @media (min-width: 576px) {
        margin-bottom: 40px;
      }
      @media (min-width: 992px) {
        margin-bottom: 0;
      }
    }
    @media (min-width: 992px) {
      padding-bottom: 40px;
    }
  }
  .footerWidgetArea {
    padding-top: 30px;
    padding-bottom: 30px;
    .m-widget-1 {
      grid-area: menu_1;
    }
    .m-widget-2 {
      grid-area: menu_2;
    }
    .m-widget-3 {
      grid-area: menu_3;
    }
    .m-widget-4 {
      grid-area: menu_4;
    }
    .sm-border {
      width: 100%;
      height: 1px;
      background: linear-gradient(
        90deg,
        rgba(255, 255, 255, 0) 0%,
        #ffffff 48.44%,
        rgba(255, 255, 255, 0) 100%
      );
      opacity: 0.1;
      margin-bottom: 30px;
      @media (min-width: 992px) {
        display: none;
      }
    }
    .widgetMenu {
      display: grid;
      gap: 12px;
      // grid-template-areas:
      //   "menu_1"
      //   "menu_2"
      //   "menu_3"
      //   "menu_4";
      // justify-content: space-between;
      grid-template-areas:
        "menu_1 menu_2"
        "menu_3 menu_4";
      padding-left: 5px;
      padding-right: 5px;
      .widgetTitle {
        margin-bottom: 15px;
        @media (min-width: 576px) {
          margin-bottom: 30px;
        }
      }
      @media (min-width: 576px) {
        display: grid;
        grid-template-areas: "menu_1 menu_2 menu_3 menu_3 menu_4";
        text-align: left;
        padding-left: 30px;
        padding-right: 30px;
      }
      @media (min-width: 992px) {
        //grid-template-columns: repeat(4, 1fr);
        //grid-template-areas: "menu_1 menu_2 menu_3 menu_3 menu_4";
        gap: 0;
        padding-left: 0;
        padding-right: 0;
      }
      .widgetList {
        .dropdown {
          display: none;
        }
        li {
          a {
            transition: all 0.2s linear;
            color: var(--clr-dark-2);
            text-decoration: none;
            font-weight: 500;
            display: block;
            margin-bottom: 7px;
            font-size: 12px;
            line-height: 17px;
            &:hover {
              color: var(--clr-green) !important;
            }
            @media (min-width: 576px) {
              margin-bottom: 10px;
              font-size: 14px;
              line-height: 22px;
            }
          }
          &:last-child {
            a {
              margin-bottom: 0 !important;
            }
          }
        }
      }
    }
    @media (min-width: 576px) {
      padding-top: 40px;
      padding-bottom: 40px;
    }
  }
