  .copyright {
    padding-top: 30px;
    padding-bottom: 30px;
    @media (min-width: 992px) {
      padding-top: 40px;
      padding-bottom: 40px;
    }
    color: var(--clr-grayDark);
    .copyrightText {
      text-align: center;
      span {
        display: inline-block;
        @include fs14();
      }
      img {
        display: block;
        margin: 0 auto 10px;
        @media (min-width: 576px) {
          display: inline-block;
          margin: 0;
          margin-right: 10px;
        }
      }
      @media (min-width: 576px) {
        display: flex;
      }
    }
  }
  .Careers {
    a {
      transition: all 0.3s linear;
      &:hover {
        background-color: #cc004b !important;
      }
    }
  }
  .back-to-top-con {
    position: fixed;
    width: 60px;
    height: 120px;
    bottom: 0;
    right: 0;
    opacity: 0;
    z-index: 999;
    transition: all 0.3s linear;
    &.make-visible {
      opacity: 1;
    }
    .back-to-top {
      width: 40px;
      height: 40px;
      margin-left: 0;
      margin-bottom: 35px;
      background-image: url(../../assets/images/arrow-top.png);
      text-indent: -9999px;
      &:hover {
        background-image: url(../../assets/images/arrow-top-hover.png);
      }
    }
  }
}
.footer-social-list {
  .list-inline-item:not(:last-child) {
    margin-right: 15px;
  }
  .list-inline-item:empty {
    display: none;
  }
}
